.profile-container{
    margin-top: 5vh;
}
.user-info{
    display: flex;
    align-items: center;
}
.profile {
    .profile-header {
        text-align: center;
        margin: 50px;
    }
    .profile-content {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .passWord {
        box-shadow: 2px 2px 2px 2px grey;
        padding: 30px;
        margin-bottom: 50px;
        .userName-text {
            font-size: 20px;
            margin: 10px 0 10px 0;

        }
        .change-password {
            font-size: 24px;
            color: #e8833a;
            font-weight: bold;
            margin: 30px 0 30px 0;
        
        }
        .input-username {
            width: 200px;
            border-color: #c3cfd9;
            border-style: solid;
            height: 20px;
            border-radius: 5px;
        }
        .save-changes {
            background-color: #6558f5;
            padding: 8px;
            color: white;
            border-radius: 5px;
            display: block;
            margin-top: 20px;
            align-items: center;
        }
        .button-wrapper {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    .userName {
        margin-right: 60px;
        box-shadow: 2px 2px 2px 2px grey;

        .input-username {
            width: 200px;
            border-color: #c3cfd9;
            border-style: solid;
            height: 20px;
            border-radius: 5px;
        }
        .userName-text {
            font-size: 20px;
            margin: 10px 0 10px 0;
        }
        .save-changes {
            background-color: #6558f5;
            padding: 8px;
            color: white;
            border-radius: 5px;
            display: block;
            margin-top: 20px;
            align-items: center;
        }
        .button-wrapper {
            display: flex;
            justify-content: center;
            align-items: center;
        }
        }
    }

