@import './Main.scss';


  .header-container {
    position: sticky;
    background-color: $secondary;
    width: 100%;
    height: 10vh;
    top: 0;
    margin-top: -20px;
    a {
      color: $white;
      text-decoration: none;
      margin: 0 10px;
    }
    
  
  }
  .loggedOut,
  .loggedIn {
    padding: 0 10px;
    box-sizing: border-box;
    height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    div {
      display: flex;
      font-size: 19px;
      margin: 0 10px;
      border: 1px solid transparent;
      outline: none;
      align-items: flex-end;
      svg {
        margin-top: -5px;
      }
    }
  }
  #name{
    font-size: 25px;
  }
  #chat{
    margin-top: 1px;
  }
  nav{
    .profile-pic{
      width: 50px;
      height: 50px;
      background-size: cover;
      background-position: center;
      border-radius: 100%;
    }
  }

.search-container{
    height:8vh;
    position:sticky;
    top:10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to top, rgba(159,236,237,1) 0%, rgba(1,205,207,1) 20%, rgba(0,87,146,1) 78%);
}

.search{
  height: 60%;
  width: 25%;
  background-color: white;
  border-radius: 5px;
  padding: 5px auto;
  box-sizing: border-box;
  display: flex;
  align-items: center; 
  @include tablet {
    width: 40%;
  }
  @include phone {
    width: 60%;
  }

}
#input{
  width: 90%;
  line-height: 20px;
  font-size: 16px;
  border: 1px solid transparent;
  outline: none;
}
.donation-category-container{
    display: flex;
    justify-content:space-around
}


.category-container{
    position: fixed;
    left: 0;
    margin-top:20px;
    padding: 10px 5px;
    box-sizing: border-box;
    @include tablet() {
      position: fixed;
      width: 100%;
      display: flex;
      background-image:linear-gradient($white,$secondary);
      flex-direction: row;
      justify-content: space-evenly;
      padding: 10px 20px;
      box-sizing: border-box;
    }
    @include phone() {
      overflow-x: scroll;
      scroll-behavior: smooth;
      padding: 10px;
      box-sizing: border-box;
      justify-content: space-between;
    }


}

.icon-container{
    display: flex;
    font-family: 'Trochut';
    font-weight: bold;
    font-stretch: ultra-expanded;
    justify-self:center;
    color: white;
 p{
     padding-top: 15px;
 }
 @include tablet() {
  flex-direction: column;
  align-items: center;
  text-align: center;
  p {
    display: none;
  }
}

}
.icon{
    margin: 5px 8px;
    width:50px;
    height: 50px;
    border-radius:25px;
    color: white;
    padding: 10px;
    box-sizing: border-box;
    background: linear-gradient(145deg, rgba(159,236,237,1) 0%, rgba(1,205,207,1) 29%, rgba(0,87,146,1) 78%);
    box-shadow: 0 4px 8px 0 rgba(170, 109, 250, 0.2); 
  }
.icon:hover{
    border-radius: 15px;
    background-image: linear-gradient(90deg, rgb(90, 16, 187) 0%, #8293ff 52%, #503bff 100%);
    transform: scaleZ(1.25) scaleY(1.25) scaleX(1.25);
    transition: all .05s ease;
    box-shadow: 0 4px 8px 0 rgba(170, 109, 250, 0.2);
}
.category-container::-webkit-scrollbar {
  display: none;
}

.donation-container{
  width: 83vw;
  padding: 0px 10px;
  box-sizing: border-box;
  text-align: center;   
  margin-top: -50px;
  margin-left: 180px; 
  @include tablet() {
    width: 100vw;
    margin: 5vh 0 0 0;
  }
}
.donation-card-container{
    margin-top: 10vh;
    display: grid;
    grid-template-columns:repeat(auto-fit,minmax(11rem,1fr));
    grid-template-rows: auto;
    grid-row-gap: 1em;
    @include tablet() {
      grid-template-columns: repeat(auto-fit, minmax(10rem, auto));
    }
    @include phone() {
      grid-template-columns: repeat(auto-fit, minmax(9rem, auto));
    }
  
}
.donation-card{
    width: 200px;
    font-size: 15px;
    border-radius: 15px;
    margin: 10px 0;
    border: 1px solid;
    background: linear-gradient(145deg, rgba(159,236,237,1) 0%, rgba(1,205,207,1) 29%, rgba(0,87,146,1) 78%);
    font-family: 'Trochut';
    font-weight: bold;
    font-stretch: ultra-expanded;
    justify-self:center;
    color: white;
}
.donation-card:hover{
    transform: scaleZ(1.5) scaleY(1.5) scaleX(1.5);
    transition: all .05s ease;
}
.donation-button{
    margin-left: 60%;
    border-radius: 15px;
    width: 30%;
    padding-bottom: 5px;
}
img{
    width: 100%;
    height: 180px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}
.footer-container{
    height: 50px;
    background-color: $secondary;
    margin-top: 300px;
    text-align: center;
    color: $white;
  
}


